#sudoku-grid {
    padding-left: 20vw;
    display: grid;
    grid-template-columns: repeat(9, 1fr); 
    min-width: 33vw;
    min-height: 33vw;
}

.grid-cell:nth-child(3n) {
    border-right: black .3rem solid;
}

.grid-cell {
    border: grey .05rem solid;
    text-align: center;
    place-items: center;
    color: black;
    
}

.grid-cell-hint {
    border: grey .05rem solid;
    padding: 0.5rem;
    padding-top: .75rem;
    color: rgb(70, 70, 70);
    text-align: center;
    place-items: center;
}
.grid-cell-hint:nth-child(3n) {
    border-right: black .3rem solid;
}


.tborder {
    border-top: black .3rem solid
}

.bborder {
    border-bottom: black .3rem solid
}

.lborder {
    border-left: black .3rem solid
}

.grid-cell:focus {
    animation: rainbow-cell infinite 1s;
    border: none;

}




input {
    outline: none;
    font-size: 1.5rem;
    font-weight: bold;
    overscroll-behavior: contain;

}

@keyframes rainbow-cell {
    0% {
        border:  inset lightblue;
    }

    6% {
        border:  inset cyan;
    }

    13% {
        border:  inset indigo;
    }

    20% {
        border:  inset blueviolet;
    }

    25% {
        border:  inset fuchsia;
    }

    32% {
        border:  inset red;
    }

    39% {
        border:  inset indianred;
    }

    44% {
        border:  inset lightcoral;
    }

    50% {
        border:  inset salmon;
    }

    56% {
        border:  inset lightgoldenrodyellow;
    }

    62% {
        border:  inset yellow;
    }

    65% {
        border:  inset lightyellow;
    }

    70% {
        border:  inset yellowgreen;
    }

    75% {
        border:  inset lightgreen;
    }

    78% {
        border:  inset green;
    }

    85% {
        border:  inset lightskyblue;
    }

    92% {
        border:  inset lightcyan;
    }

    100% {
        border:  inset lightblue;
    }
}


h3 {
    margin-block-start: 0;
    margin-block-end: 0;
    margin-inline-start: 0;
    margin-inline-end: 0;

}

input[type=number] {
    -moz-appearance: textfield;
  }

  /* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.error {
    color: red
}